"use strict";
//setup
import { OpenAI } from "openai";

//functions

//generic call to API
export async function call(messages, apiKey) {
  const openai = new OpenAI({
    apiKey,
    dangerouslyAllowBrowser: true,
  });

  const completion = await openai.chat.completions.create({
    model: "gpt-4o-mini",
    messages,
  });

  return completion.choices[0].message.content;
}

//generates a prompt sentence
export async function getSentence(query, apiKey) {
  const messages = [
    {
      role: "user",
      content:
        "Give me a plain text JSON with a simple  English sentence featuring this word, at this difficulty. Obey all notes. Also tell me the target language with this code.",
    },
    {
      role: "user",
      content:
        '{"languageCode": "zh", "word": "fruit", "difficulty": "beginner", "notes": "ask a question", "difficulty": "beginner"}',
    },
    {
      role: "assistant",
      content:
        '{"targetLanguage": "Chinese", "en": "What is your favorite fruit to eat?"}',
    },
    {
      role: "user",
      content: JSON.stringify(query),
    },
  ];
  return await call(messages, apiKey);
}

//generates feedback
export async function getCorrections(query, apiKey) {
  const messages = [
    {
      role: "user",
      content:
        "Give a JSON with my translation attempt and corrections on my attempt. If my attempt is perfect, leave the corrections array empty. Include transliterations if applicable.",
    },
    {
      role: "user",
      content:
        '{"targetLanguage": "zh", "originalSentence": "This dog has brown fur.", "attempt": "这个狗有棕色毛发"}',
    },
    {
      role: "assistant",
      content: `{"attempt": "这个狗有棕色毛发 (zhège gǒu yǒu zōngsè máofà)", "correctedAttempt": "这只狗有棕色的毛发。(zhèzhī gǒu yǒu zōngsède máofà.)", "corrections": ["Use \\"只 (zhī)\\" instead of \\"个 (ge)\\" because \\"只\\" is the measure word for animals.", "Insert \\"的 (de)\\" into \\"棕色的毛发 (zōngsède máofà)\\" to connect the adjective to the noun.]}`,
    },
    { role: "user", content: JSON.stringify(query) },
  ];
  return await call(messages, apiKey);
}
